html,
body {
  margin: 0;
  padding: 0;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
}

@media only screen and (max-width: 769px) {
  .Main {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    background: url(/veber/static/media/bg-mobile_portrait.98db6a63.jpg) no-repeat
      center center fixed;
    background-size: cover;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }
}

@media only screen and (min-width: 768px) {
  .Main {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    background: url(/veber/static/media/bg-min.c331c969.jpg) no-repeat
      center center fixed;
    background-size: cover;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }
}



.Footer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: auto;
  background: #181818;
  color: white;
}

.ButtonBox {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
}

.Button {
  width: 180px;
  background-color: #e0e0e0;
  border-radius: 16px;
  text-decoration: none;
  margin: 5px;
}

.Column {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-align-items: center;
          align-items: center;
  margin: 10px 25px 0px;
}

@media only screen and (min-width: 768px) {
  div.Column {
    margin: 10px 0px 0px;
  }
}

.ColumnText {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 10px;
  white-space: nowrap;
}

.Veber-logo {
  height: 28vmin;
  pointer-events: none;
  top: 10px;
  left: 10px;
  position: absolute;
  background: #e0e0e082;
}

break {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  width: 0px; 
  height: 0px; 
  overflow: hidden;
}

.lastItem {
  padding-bottom: 20px;
}

.buttonText {
  padding-left: 14px;
  text-transform: lowercase;
}

a.Button {
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
